import {
  IonApp,
  IonContent,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonItem,
  IonLabel,
  IonDatetime,
} from "@ionic/react";
import React, { useState } from "react";
import BioRhythmCard from "./Components/biorhythm";
import useLocalStorage from "./hooks";

function App() {
  const [dateInput, setDateInput] = useLocalStorage("dateInput", "");
  const [targetDate, setTargetDate] = useState(new Date().toISOString());

  return (
    <IonApp>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Biorhythm Calculator</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent className="ion-padding">
        { dateInput && 
          <BioRhythmCard dateInput={dateInput} targetDate={targetDate} />
        }
        <IonItem>
          <IonLabel position="floating">DOB : </IonLabel>
          <IonDatetime
            displayFormat="D MMM YYYY"
            value={dateInput}
            onIonChange={(event) => setDateInput(event.target.value)}
          />
        </IonItem>
        <IonItem>
          <IonLabel position="floating">Target Date : </IonLabel>
          <IonDatetime
            displayFormat="D MMM YYYY"
            value={targetDate}
            onIonChange={(event) => setTargetDate(event.target.value)}
          />
        </IonItem>
      </IonContent>
    </IonApp>
  );
}

export default App;
