import dayjs from "dayjs";
import React from "react";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  ReferenceLine,
  CartesianGrid,
} from "recharts";
import { CalculateBioRhythmSeries } from "../calculations";
import "./BiorhythmCharts.css";

function formatDate(isoString) {
  return dayjs(isoString).format("D MMM");
}

function BiorhythmCharts({ dateInput, targetDate }) {
  const startDay = dayjs(targetDate).subtract(15, "days").toISOString();
  const data = CalculateBioRhythmSeries(dateInput, startDay, 31).map(
    (item) => ({ ...item, date: formatDate(item.date) })
  );
  return (
    <ResponsiveContainer className="biorhythm-charts" width="100%" height={200}>
      <LineChart data={data}>
        <XAxis
          dataKey="date"
          ticks={[data[3].date, data[15].date, data[27].date]}
        />
        <CartesianGrid vertical={false} strokeDasharray="3 3" />
        <ReferenceLine x={data[15].date} />
        <Line dot={false} type="natural" dataKey="physical" stroke="green" />
        <Line dot={false} type="natural" dataKey="emotional" stroke="blue" />
        <Line dot={false} type="natural" dataKey="intellectual" stroke="red" />
      </LineChart>
    </ResponsiveContainer>
  );
}

export default BiorhythmCharts;
