import React from "react";
import dayjs from "dayjs";
import {
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
} from "@ionic/react";
import { CalculateBioRhythms } from "../calculations";
import BiorhythmCharts from "./BioRhythmCharts";
import "./Biorhythm.css";

function formatDate(isoString) {
  return dayjs(isoString).format("D MMM YYYY");
}

function BioRhythmCard({ dateInput, targetDate }) {
  console.log("BioRhythmCard running");
  const { physical, emotional, intellectual } = CalculateBioRhythms(dateInput,targetDate);
  return (
    <IonCard className="ion-text-center">
      <IonCardHeader>
        <IonCardTitle>{formatDate(targetDate)}</IonCardTitle>
      </IonCardHeader>
      <IonCardContent>
        <BiorhythmCharts dateInput={dateInput} targetDate={targetDate} />
        <p className="physical">Physical : {physical.toFixed(4)}</p>
        <p className="emotional">Emotional : {emotional.toFixed(4)}</p>
        <p className="intellectual">Intellectual : {intellectual.toFixed(4)}</p>
      </IonCardContent>
    </IonCard>
  );
}

export default BioRhythmCard;
